  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  display: inline-block;
  position: relative;
}
.b-customers .b-cust_logo:after,
.b-customers .b-cust_logo:before {
  top: 0;
  width: inherit;
  height: inherit;
  display: block;
  content: '';
  position: absolute;
}
.b-customers .b-cust_logo:before {
  left: 0;
  filter: alpha(opacity=100);
  opacity: 1;
  background-position: 0px 100%;
}
.b-customers .b-cust_logo:after {
  right: -10;
  filter: alpha(opacity=0);
  opacity: 0;
  background-position: 0px 0px;
}
.b-customers .b-cust_logo:hover:before {
  left: -10px;
  filter: alpha(opacity=0);
  opacity: 0;
}
.b-customers .b-cust_logo:hover:after {
  right: 0;
  filter: alpha(opacity=100);
  opacity: 1;
}
